import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { Storage } from '@ionic/storage';
import {of as observableOf} from 'rxjs';
import {map, tap} from 'rxjs/operators';
import {environment} from '../config/environment';
import {webtestnetissueconfig, weblivenetissueconfig} from '../config/webissueconfig';
import { ChangellyCalls } from '../../providers/changelly';

declare var dashcore;

@Injectable({
  providedIn: 'root'
})

export class PaytmService {
  public token: any;
  url: string ;

  constructor(public http: Http, public storage: Storage,
              public changellyCalls: ChangellyCalls             

  	) {
     this.url = environment.hosteddomain ;

  }


  initiate (details, myurl) {

   return new Promise((resolve, reject) => {



             let headers = new Headers();
             headers.append('Content-Type', 'application/json');

            this.http.post(myurl , JSON.stringify(details), {headers: null})
              .subscribe(res => {
                let data = res.json();
                resolve(data);

              }, (err) => {
                reject(err);
              });

        });


  }


  generateSignature (details) {

   return new Promise((resolve, reject) => {


           this.storage.get(environment.storageuniq+'token').then((value) => {

             this.token = value;
             let headers = new Headers();
             headers.append('Authorization', this.token);
             headers.append('Content-Type', 'application/json');

            this.http.post(this.url + '/webuser/generatesignature', JSON.stringify(details), {headers: headers})
              .subscribe(res => {
                let data = res.json();
                resolve(data);

              }, (err) => {
                reject(err);
              });

        });
        });


  }



 
}
