  async accept() {
   this.loggedinuser = await this.termsuserservice.getloggedinuser();

    if( !this.loggedinuser || this.loggedinuser.uid == '') {
       return;
    }

 

    var data = {
     email: this.loggedinuser.email,
     googleid:  this.loggedinuser.uid,
     name: this.loggedinuser.displayName,
     termsagreed: 'yes'
   };

   this.termsuserservice.createaccount(data).then((data1: any) => {
       if(data1) {
     this.useraccount = data1;
      this.storage.set(environment.storageuniq+'useraccount', JSON.stringify(this.useraccount) ).then(x=> {
       this.termsuserservice.reflectuseraccount();
       })  ;

     this.storage.set(environment.storageuniq+'token', "WebKey "+ this.useraccount.token);
     }

     this.gotohome() ;

   });;


  }

  async setdashprice() {
   this.loggedinuser = await this.termsuserservice.getloggedinuser();

    if( !this.loggedinuser || this.loggedinuser.uid == '') {
        this.presentAlert("", "Login to use ", "Error code:"+environment.err.W1230.code);
       return;
    }

    if(Number.isNaN(this.setdashpriceinusd)) {
       this.presentAlert("", "Set valid price in numbers  ", "Error code:"+environment.err.W1230.code);
       return;
    }

 

    var data = {
     dashprice: this.setdashpriceinusd
   };

   this.webuserservice.setdashprice(data).then((data1: any) => {
       if(data1) {
     this.useraccount = data1;
      this.storage.set(environment.storageuniq+'useraccount', JSON.stringify(this.useraccount) ).then(x=> {
       this.termsuserservice.reflectuseraccount();
       })  ;

     this.storage.set(environment.storageuniq+'token', "WebKey "+ this.useraccount.token);
     }

     this.gotohome() ;

   }).catch(err =>  {
        this.presentAlert("", "Set dash price failed" , "Error code:"+environment.err.W1230.code);
       return;

   });;


  }

