openpaytm() {
var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json'  );

    this.http.post('https://dashrevert.belavaditech.com/paynow', JSON.stringify(this.paytmform), { headers: headers })
       .subscribe(async (res1: any) => {


    let popupWin = window.open( '', '_blank', 'top=0,left=0,height=600px,width=600px');
    popupWin.document.write(res1._body);
  });
}

  dynamicopen(xx): void {
    let printContents, popupWin;
    printContents = xx;  //document.getElementById('print-section').innerHTML;
    popupWin = window.open('', '_blank', 'top=0,left=0,height=400px,width=400px');
    popupWin.document.write(xx);
    //popupWin.document.close();
  }

  showdynamicopen(init, txnToken): void {
    let printContents, popupWin;
    printContents = `<html>
                                <head>
                                    <title>Show Payment Page</title>
                                </head>
                                <body>
                                    <center>
                                        <h1>Please do not refresh this page...</h1>
                                    </center>
                                    <form method="post" action="https://securegw-stage.paytm.in/theia/api/v1/showPaymentPage?mid=${init.mid}&orderid=${init.orderid}" name="paytm">
                                        <table border="1">
                                            <tbody>
                                                <input type="hidden" name="mid" value="${init.mid}">
                                                    <input type="hidden" name="orderId" value="${init.orderId}">
                                                    <input type="hidden" name="txnToken" value="${txnToken}">
                                         </tbody>
                                      </table>
                                                    <script type="text/javascript"> document.paytm.submit(); </script>
                                   </form>
                                </body>
    </html>`;


    popupWin = window.open('', '_blank', 'top=0,left=0,height=600px,width=600px');
    popupWin.document.write(printContents);
    //popupWin.document.close();
  }

